  .grid .item a {
    position: relative;
    background-size: cover; }
    .grid .item a span {
      position: absolute;
      bottom: 1.5rem;
      left: 1rem;
      writing-mode: vertical-lr;
      transform: rotate(180deg);
      font-size: 2.2rem;
      font-weight: 800;
      letter-spacing: 1rem;
      color: black;
      text-transform: uppercase; }
    .grid .item a:hover {
      box-shadow: 0 0 10px 0 rgba(50, 50, 50, 0.3);
      cursor: pointer; }
    .grid .item a:after {
      content: '50%';
      position: absolute;
      top: 0;
      right: 0;
      width: 4rem;
      height: 2.5rem;
      padding: .4rem;
      background-color: #EA0029;
      text-align: center;
      font-weight: 700;
      color: #FFFFFF; }
    .grid .item a:before {
      content: 'Black Friday';
