      .grid .item-1 a:first-child {
        background-image: url("/build/img/13.webp"); }
      .grid .item-1 a:nth-child(2) {
        background-image: url("/build/img/14.webp");
        background-position: bottom center; }
      .grid .item-1 a:last-child {
        background-image: url("/build/img/15.webp");
        background-position: center center;
        height: 20rem;
        grid-column: 1 / 3; }
  .grid .item-2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem; }
    .grid .item-2 a {
      height: 30rem;
      background-position: center center; }
      @media (min-width: 768px) {
        .grid .item-2 a {
          height: 100%; } }
      .grid .item-2 a:first-child {
        background-image: url("/build/img/1.jpg"); }
      .grid .item-2 a:last-child {
        background-image: url("/build/img/2.jpg"); }
  .grid .item-3 {
    display: grid; }
    .grid .item-3 a {
